@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Playfair+Display:wght@400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: 'Inter', 'SF Pro Display', 'Helvetica Neue', system-ui, -apple-system, sans-serif;
  }
  
  body {
    margin: 0;
    font-family: 'Inter', 'SF Pro Display', 'Helvetica Neue', -apple-system, BlinkMacSystemFont, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
    color: #1f2937;
    line-height: 1.7;
  }

  * {
    box-sizing: border-box;
  }

  h1, h2, h3, h4, h5, h6 {
    font-family: 'Playfair Display', Georgia, serif;
    font-weight: 600;
    letter-spacing: -0.02em;
  }
}

@layer components {
  /* Modern Buttons */
  .btn-primary {
    @apply bg-gradient-to-r from-primary-600 to-primary-500 active:from-primary-700 active:to-primary-600 text-white font-semibold py-4 px-8 rounded-2xl transition-all duration-150 shadow-lg active:shadow-md border-0;
  }
  
  .btn-primary:active {
    transform: scale(0.98);
  }

  .active\:scale-98:active {
    transform: scale(0.98);
  }
  
  .btn-secondary {
    @apply bg-white active:bg-sage-50 text-sage-800 font-medium py-4 px-8 rounded-spa border-2 border-sage-200 active:border-primary-400 transition-all duration-150 shadow-spa;
  }

  .btn-secondary:active {
    transform: scale(0.98);
  }

  .btn-danger {
    @apply bg-danger hover:bg-red-600 text-white font-medium py-4 px-8 rounded-spa transition-all duration-300 shadow-spa border-0;
  }
  
  /* Elegant Cards - Spa aesthetic */
  .card {
    @apply bg-white/90 backdrop-blur-sm rounded-spa-lg shadow-spa border border-sage-100 p-6 transition-all duration-200;
  }

  .card:active {
    transform: scale(0.98);
  }

  .card-minimal {
    @apply bg-white/80 backdrop-blur-sm rounded-spa border border-sage-100 p-5 shadow-sm;
  }
  
  /* Elegant Input Fields */
  .input-field {
    @apply w-full px-5 py-4 border-2 border-sage-200 rounded-spa focus:ring-2 focus:ring-primary-400/30 focus:border-primary-400 outline-none transition-all duration-300 bg-white placeholder:text-sage-400 text-text-primary;
  }

  .input-field:focus {
    box-shadow: 0 4px 12px rgba(22, 163, 74, 0.1);
  }
  
  /* Wellness-themed mood selection */
  .mood-button {
    @apply flex flex-col items-center justify-center p-5 rounded-spa border-2 border-sage-200 transition-all duration-300 cursor-pointer bg-white/90 backdrop-blur-sm hover:border-primary-400 hover:shadow-spa;
  }
  
  .mood-button.active {
    @apply border-primary-500 bg-gradient-to-br from-primary-50 to-mint-50 shadow-spa-lg;
  }

  /* Elegant Typography */
  .heading-spa {
    @apply font-serif text-sage-900 tracking-tight;
  }

  .text-soft {
    @apply text-sage-600;
  }

  /* Smooth Animations */
  .fade-in {
    animation: fadeIn 0.6s ease-out;
  }

  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }

  .slide-up {
    animation: slideUp 0.5s ease-out;
  }

  @keyframes slideUp {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
  }

  .float {
    animation: float 3s ease-in-out infinite;
  }

  @keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }

  /* Page Transitions - App-like */
  .page-transition-enter {
    animation: pageEnter 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }

  @keyframes pageEnter {
    from {
      opacity: 0;
      transform: translateX(20px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

  /* Smooth touch interactions */
  * {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
  }

  button, a {
    -webkit-user-select: none;
    user-select: none;
  }

  /* Crisis banner - calming but visible */
  .crisis-banner {
    @apply bg-red-50/80 backdrop-blur-sm border-l-4 border-danger p-5 rounded-spa shadow-spa;
  }

  /* Gradient overlays */
  .gradient-overlay {
    background: linear-gradient(135deg, rgba(240, 253, 244, 0.9) 0%, rgba(220, 252, 231, 0.8) 100%);
  }

  /* Phone Frame Styling */
  .phone-frame {
    box-shadow: 
      0 20px 60px rgba(0, 0, 0, 0.5),
      0 0 0 1px rgba(255, 255, 255, 0.1),
      inset 0 0 0 2px rgba(255, 255, 255, 0.05);
  }

  /* Custom scrollbar for phone view */
  .phone-frame ::-webkit-scrollbar {
    width: 6px;
  }

  .phone-frame ::-webkit-scrollbar-track {
    background: transparent;
  }

  .phone-frame ::-webkit-scrollbar-thumb {
    background: rgba(107, 126, 110, 0.3);
    border-radius: 10px;
  }

  .phone-frame ::-webkit-scrollbar-thumb:hover {
    background: rgba(107, 126, 110, 0.5);
  }
}
